@charset "UTF-8";

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  margin: 0;
  font-size: 16px;
  min-width: 320px;
  line-height: 17px;
  font-family: "Inter", sans-serif;
  color: #0E0E0E;
}

body input,
body textarea {
  border: #666 1px solid;
  outline: none;
}

b,
strong {
  font-weight: 700;
}

::placeholder {
  color: #666;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-style: "Inter", sans-serif;
  font-size: 16px;
  font-weight: 500;
  color: #0E0E0E;
}

.header {
  display: flex;
  justify-content: center;
}

.intro_wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  width: 100%;
}

.container {
  max-width: 1320px;
  display: flex;
  flex-direction: column;
  width: 100%;
  position: relative;
}

.intro {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.intro_menu {
  display: none;
}

.intro_info {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.intro_info_title {
  text-align: center;
  font-size: 70px;
  max-width: 650px;
  line-height: 80px;
  padding: 120px 0 60px;
}

.logo_wrap {
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.logo_wrap span {
  font-size: 20px;
}

.logo_wrap img {
  padding-top: 10px;
  height: 100px;
}

.btn {
  padding: 13px 61px 15px 36px;
  text-decoration: none;
  line-height: 26px;
  position: relative;
  color: #fff;
  background-color: #0E0E0E;
}

.circle {
  position: absolute;
  left: -21%;
  width: 800px;
  height: 800px;
  border-radius: 50%;
  background-color: #f5f5f5;
  z-index: -1;
  top: -52%;
}

.btn_circle::after {
  content: "‚óè";
  font-size: 12px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 20%;
}

.nav .list_item {
  color: #0E0E0E;
  text-decoration: none;
  position: relative;
}

.nav .list_item::after {
  content: "";
  position: absolute;
  height: 2px;
  background: black;
  width: 0%;
  top: 58%;
  left: -10%;
  transform: translateY(-50%);
  transition: width 0.2s linear;
}

.nav .list_item.active::after {
  width: 120%;
}

.nav .link_item {
  color: #0E0E0E;
  text-decoration: none;
  position: relative;
  transition: all 0.666s;
  padding: 0 10px;
}

.nav .link_item:hover {
  color: black;
}

.nav .link_item::after,
.nav .link_item::before {
  transition: all 0.666s;
}

.nav .link_item::after {
  position: absolute;
  content: "";
  bottom: 0;
  right: 0;
  left: 0;
  top: 25px;
  margin: auto;
  width: 0%;
  height: 2px;
  background-color: #000;
}

.nav .link_item:hover::after {
  width: 100%;
}

.nav .list_item {
  display: inline;
}

.social_links_wrap a:not(:last-child) {
  margin-right: 20px;
}

.social_link_list {
  display: flex;
}

.social_link_list_item {
  display: block;
  position: relative;
  background-color: #fff;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  transition: 0.666s;
}

.social_link_list_item:not(:last-child) {
  margin-right: 20px;
}

.social_link_list_item:hover {
  transform: translate(0, -10%);
  box-shadow: 0 5px 4px rgba(0, 0, 0, 0.5);
}

.social_links_wrap ul li:nth-child(1):hover {
  background-color: #053eff;
}

.social_links_wrap ul li:nth-child(1):hover .behanse_svg {
  fill: #fff;
}

.social_links_wrap ul li:nth-child(2):hover {
  background-color: #1DA1F2;
}

.social_links_wrap ul li:nth-child(2):hover .twitter_svg {
  fill: #fff;
}

.social_links_wrap ul li:nth-child(3):hover {
  background-color: #4267B2;
}

.social_links_wrap ul li:nth-child(3):hover .facebook_svg {
  fill: #fff;
}

.social_links_wrap ul li:nth-child(4):hover {
  background-color: #C13584;
}

.social_links_wrap ul li:nth-child(4):hover .inst_svg {
  fill: #fff;
}

.social_links_wrap ul li:nth-child(5):hover {
  background-color: #E60023;
}

.social_links_wrap ul li:nth-child(5):hover .pinterest_svg {
  fill: #fff;
}

.facebook_svg path,
.twitter_svg path,
.inst_svg path,
.behanse_svg path,
.pinterest_svg path {
  transition: 200ms;
  transition-timing-function: ease-in-out;
}

.social_link {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 25px;
}

.nav_wrap {
  margin: 0 75px 0 auto;
}

@media only screen and (max-width: 1400px) {
  body {
    overflow: hidden;
  }

  .container {
    max-width: 1140px;
  }

  .circle {
    left: -40%;
    top: -45%;
  }
}

@media only screen and (max-width: 1200px) {
  .container {
    max-width: 960px;
  }

  .container.active {
    padding: 0;
    overflow: hidden;
  }

  .intro_info.active {
    display: none;
  }

  .intro_wrapper {
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  .intro_menu {
    display: flex;
    height: 25px;
    width: 25px;
    position: absolute;
    right: 0;
  }

  .intro_menu span {
    position: absolute;
    width: 25px;
    height: 2px;
    background-color: #0E0E0E;
    top: 50%;
    transition: 0.5s;
  }

  .intro_menu span:nth-of-type(2) {
    top: calc(50% + 4px);
  }

  .intro_menu span:nth-of-type(3) {
    top: calc(50% - 4px);
  }

  .intro_menu.active span:nth-of-type(1) {
    transform: rotate(1turn);
    opacity: 0;
  }

  .intro_menu.active span:nth-of-type(2) {
    top: 50%;
    transform: rotate(45deg);
  }

  .intro_menu.active span:nth-of-type(3) {
    top: 50%;
    transform: rotate(-45deg);
  }

  .social_links_wrap {
    display: flex;
    justify-content: space-around;
    transition: 0.5s;
    margin: auto;
  }

  .nav_wrap {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;
    align-items: flex-start;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #F7F1F1;
    transform: translateX(100%);
    transition: 0.5s;
  }

  .nav_wrap .nav_list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .nav_wrap .nav_list li {
    margin-top: 100px;
  }

  .nav_wrap.active {
    transform: translateX(0%);
  }
}

@media only screen and (max-width: 992px) {
  .container {
    max-width: 720px;
  }

  .intro_info_title {
    font-size: 60px;
    max-width: 550px;
    line-height: 60px;
  }

  .circle {
    width: 600px;
    height: 600px;
    left: -40%;
    top: -25%;
  }
}

@media only screen and (max-width: 768px) {
  .container {
    max-width: 540px;
  }

  .intro_info_title {
    font-size: 50px;
    max-width: 500px;
    line-height: 50px;
  }

  .circle {
    left: -70%;
    top: -20%;
  }

  .nav_wrap {
    transform: translateX(150%);
  }

  .nav_wrap .nav ul {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

@media only screen and (max-width: 576px) {
  .container {
    padding: 0 15px;
  }

  .intro_info_title {
    font-size: 30px;
    max-width: 400px;
    line-height: 40px;
  }

  .circle {
    left: -65%;
    top: -30%;
  }

  .intro_menu {
    right: 15px;
  }

  .social_links_wrap {
    display: none;
  }
}

@media only screen and (max-width: 450px) {
  .circle {
    width: 400px;
    height: 400px;
    left: -40%;
    top: -9%;
  }
}

@media only screen and (max-width: 320px) {
  .circle {
    left: -65%;
    top: -10%;
  }
}